import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../shared/services/api.service';
import { routerTransition } from '../../router.animations';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
    // tslint:disable-next-line:component-selector
    selector: 'app-itemForm',
    templateUrl: './itemForm.component.html',
    styleUrls: ['./itemForm.component.scss'],
    animations: [routerTransition()]
})
export class ItemFormComponent implements OnInit {
    selectedItem: Item = new Item();
    itemForm: FormGroup;
    submitBtnName = 'Save Item';
    formTitle = 'Add Item';
    formIcon = 'fa-plus';
    image: any;
    imageUri: any = '../../../assets/images/album_cover.png';
    constructor(
        private apiService: ApiService,
        private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private toastr: ToastrService
    ) {
        this.itemForm = formBuilder.group({
            itemName: new FormControl('', Validators.required),
            itemDescription: new FormControl('', Validators.required),
            itemPrice: new FormControl('', Validators.required),
            file: ''
        });
    }

    ngOnInit() {
        this.route.params.subscribe((params) => {
            const id = params['id'];
            const flag = params['flag'];
            this.submitBtnName = flag === 'D' ? 'Delete Item' : flag === 'M' ? 'Update Item' : 'Save Item';
            this.formTitle = flag === 'D' ? 'Delete Item' : flag === 'M' ? 'Update Item' : 'Add Item';
            this.formIcon = flag === 'D' ? 'fa-trash' : flag === 'M' ? 'fa-edit' : 'fa-plus';
            this.selectedItem.op_flag = flag;
            console.log('idid::', id);
            if (id !== '0') {
                const itmResp = null;
                this.apiService.getItemById(id).subscribe((data: any) => {
                    console.log('itmResp:', data);
                    if (data) {
                        this.selectedItem.id = data.id;
                        this.selectedItem.itemName = data.itemName;
                        this.selectedItem.itemDescription = data.itemDescription;
                        this.selectedItem.itemPrice = data.itemPrice;
                        this.selectedItem.itemImageUri = data.itemImageUri;
                        this.imageUri = data.itemImageUri;
                    } else {
                        this.selectedItem.id = 0;
                        this.selectedItem.itemName = null;
                        this.selectedItem.itemDescription = null;
                        this.selectedItem.itemPrice = null;
                        this.selectedItem.itemImageUri = null;
                        this.imageUri = '../../../assets/images/album_cover.png';
                    }
                });
                console.log('itmResp::', itmResp);
            } else {
                this.selectedItem.id = 0;
                this.selectedItem.itemName = null;
                this.selectedItem.itemDescription = null;
                this.selectedItem.itemPrice = null;
                this.selectedItem.itemImageUri = null;
                this.imageUri = '../../../assets/images/album_cover.png';
            }
        });
    }

    onFileChange(event) {
        console.log('onFileChange::');
        if (event.target.files && event.target.files[0]) {
            console.log('onFileChange::');
            const reader = new FileReader();
            reader.onload = (ev: any) => {
                this.selectedItem.itemImageUri = ev.target.result;
                console.log('imageUri::', this.imageUri);
                this.imageUri = ev.target.result;
            };
            reader.readAsDataURL(event.target.files[0]);
        }
    }

    saveDeleteItem() {
        if (this.selectedItem.op_flag === 'D') {
            this.apiService.deleteItem(this.selectedItem.id).subscribe((res) => {
                console.log('res', res);
                if (JSON.parse(JSON.stringify(res))) {
                    this.toastr.success('Success', 'Item Deleted Successfully !', {
                        closeButton: true,
                        positionClass: 'toast-bottom-right'
                    });
                    this.resetForm();
                } else {
                    this.toastr.error('Failed', 'Unable to deleted item !', {
                        closeButton: true,
                        positionClass: 'toast-bottom-right'
                    });
                }
            });
        } else if (this.selectedItem.op_flag === 'N') {
            this.apiService.saveItem(this.selectedItem).subscribe((res) => {
                if (JSON.parse(JSON.stringify(res)).status === 'success') {
                    this.toastr.success('Success', 'Item Save Successfully !', {
                        closeButton: true,
                        positionClass: 'toast-bottom-right'
                    });
                    this.resetForm();
                } else {
                    this.toastr.error('Failed', 'Unable to save item !', {
                        closeButton: true,
                        positionClass: 'toast-bottom-right'
                    });
                }
            });
        } else {
            this.apiService.updateItem(this.selectedItem).subscribe((res) => {
                if (JSON.parse(JSON.stringify(res)).status === 'success') {
                    this.toastr.success('Success', 'Item Updated Successfully !', {
                        closeButton: true,
                        positionClass: 'toast-bottom-right'
                    });
                    this.resetForm();
                } else {
                    this.toastr.error('Failed', 'Unable to update item !', {
                        closeButton: true,
                        positionClass: 'toast-bottom-right'
                    });
                }
            });
        }
    }

    resetForm() {
        this.selectedItem = {
            id: null,
            itemName: null,
            itemDescription: null,
            itemPrice: null,
            op_flag: 'N',
            itemImageUri: null
        };
        this.imageUri = '../../../assets/images/album_cover.png';
        this.submitBtnName =
            this.selectedItem.op_flag === 'D'
                ? 'Delete Item'
                : this.selectedItem.op_flag === 'M'
                ? 'Update Item'
                : 'Save Item';
        this.formTitle =
            this.selectedItem.op_flag === 'D'
                ? 'Delete Item'
                : this.selectedItem.op_flag === 'M'
                ? 'Update Item'
                : 'Add Item';
        this.formIcon =
            this.selectedItem.op_flag === 'D' ? 'fa-trash' : this.selectedItem.op_flag === 'M' ? 'fa-edit' : 'fa-plus';
        this.itemForm.markAsUntouched();
    }
}

export class Item {
    id: any;
    itemName: string;
    itemDescription: string;
    itemPrice: number;
    itemImageUri: any;
    op_flag: string;
}
